<template>
	<div class="noticelist">
		<el-collapse v-model="activeName" accordion>
		  <el-collapse-item :title="notice.impldate+' '+notice.title" :name="key" v-for="(notice,key) in noticelist">
		    <div v-html="notice.content" class="ollist">{{notice.content}}</div>
		  </el-collapse-item>
		  <!-- <el-collapse-item title="反馈 Feedback" name="2">
		    <div>控制反馈：通过界面样式和交互动效让用户可以清晰的感知自己的操作；</div>
		    <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div>
		  </el-collapse-item>
		  <el-collapse-item title="效率 Efficiency" name="3">
		    <div>简化流程：设计简洁直观的操作流程；</div>
		    <div>清晰明确：语言表达清晰且表意明确，让用户快速理解进而作出决策；</div>
		    <div>帮助用户识别：界面简单直白，让用户快速识别而非回忆，减少用户记忆负担。</div>
		  </el-collapse-item>
		  <el-collapse-item title="可控 Controllability" name="4">
		    <div>用户决策：根据场景可给予用户操作建议或安全提示，但不能代替用户进行决策；</div>
		    <div>结果可控：用户可以自由的进行操作，包括撤销、回退和终止当前操作等。</div>
		  </el-collapse-item> -->
		</el-collapse>
	</div>
</template>
<script>
export default{
	data(){
		return {
			dialogTableVisible:false,
			noticelist:[],
			activeName:''
		}
	},
	created(){
		this.getnotice();
	},
	methods:{
		async getnotice(){
			await this.$axios({
				method:'post',
				url:"/crxl/qpp/distributor/getNews",
			}).then((res)=>{
				if(res.data.code == 200){
					this.noticelist = res.data.obj;
				}else{
					this.$message(res.data.msg);
				}
			})
		}
	}
}	
</script>
<style lang="stylus">
.noticelist
	.el-collapse-item__header
		color:baseColor;
		cursor:pointer;
	.el-dialog__wrapper
		.el-dialog
			width:30%;
	.ollist
		ol
			li
				list-style-type:decimal;							
</style>


